::-webkit-scrollbar {
	width: 0;
	background: 0 0;
}

::-moz-selection {
	background: rgba(204, 204, 204, 0.18);
	color: #000;
}

::selection {
	background: rgba(204, 204, 204, 0.18);
	color: #000;
}

html body {
	_background-attachment: fixed;
}

html {
	width: 100%;
	height: 100%;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

body {
	width: 100%;
	height: 100%;
	font-family: PingFang SC, 'Hiragino Sans GB', 'Source Han Sans CN', Roboto, 'Microsoft Yahei', sans-serif;
	font-weight: 400;
	font-size: 16px;
	color: rgba(2, 0, 0, 0.6);
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
blockquote,
p,
div,
span,
ul,
ol,
li,
form,
input,
textarea,
br,
button {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
}

*,
*:after,
*:before {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5 {
	margin: 0;
}

h1 {
	font-size: 20px;
	line-height: 48px;
	margin-bottom: 24px;
}

a {
	color: #807f83;
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}

a:active,
a:hover {
	outline: 0;
}

blockquote {
	overflow: hidden;
}

ul,
ol {
	list-style: none;
}

img {
	height: auto;
	max-width: 100%;
}

ol,
ul {
	list-style: none;
}

img {
	max-width: 100%;
	height: auto;
	transition: opacity 0.2s;
}

.clearfix:before,
.clearfix:after {
	content: ' ';
	display: table;
}

.clearfix:after {
	clear: both;
}

.back-top {
	position: fixed;
	bottom: 50px;
	right: 0;
	z-index: 2;
	display: none;
}

.back-top i {
	font-size: 50px;
}

.blog-description {
	text-transform: none;
	font-weight: 400;
}

.custom-logo {
	display: inline-block;
	width: 100px;
}

.site-branding {
	position: relative;
	display: inline-block;
	z-index: 3;
	padding-top: 100px;
}

#xiaoshubao::before {
	position: fixed;
	width: 100%;
	height: 100%;
	-moz-background-size: cover;
	-o-background-size: cover;
	-webkit-background-size: cover;
	background-size: cover;
	background-position: top center;
	z-index: -999;
	content: '';
	display: block;
	background-image: url(./assets/47fb3c_.jpg);
}

#particles-js {
	position: absolute;
	top: -100px;
	width: 100%;
	height: 100%;
}

#masthead {
	position: relative;
	z-index: 2;
	display: block;
	color: #fff;
	text-align: center;
	width: 100%;
	height: 450px;
	box-sizing: border-box;
}

#main {
	background: #fff;
	position: relative;
	z-index: 2;
}

.container {
	position: relative;
	background: #fff;
	width: 800px;
	margin: 0 auto;
	overflow: hidden;
}

.decor-wrapper {
	width: 100%;
	height: 50%;
	position: absolute;
	left: 0;
	bottom: 0;
	z-index: 2;
}

.decor {
	position: absolute;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 2;
}

.decor.bottom {
	bottom: -1px;
}

.decor.top {
	top: -1px;
}

.avatar img {
	border-radius: 50%;
}

article {
	width: 800px;
	margin: 100px auto 0 auto;
}

.top-social {
	height: 32px;
	list-style: none;
	display: inline-block;
	margin-top: 20px;
}

.top-social li {
	float: left;
	margin-right: 10px;
}

.top-social img {
	height: 32px;
	width: 32px;
	background: #fff;
	border-radius: 50%;
}
