pre > code {
  color: #333;
  background: #f8f8f8;
}
pre > code {
  font-size: 12px;
  padding: 15px 12px;
  margin: 0;
  word-break: normal;
  display: block;
  overflow-x: auto;
  color: #333;
  background: #f8f8f8;
}
pre {
  font-family: Menlo, Monaco, Consolas, Courier New, monospace;
}
code {
  word-break: break-word;
  border-radius: 2px;
  overflow-x: auto;
  background-color: #fff5f5;
  color: #ff502c;
  font-size: 0.87em;
  padding: 0.065em 0.4em;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

p code {
  padding: 10px;
  margin: 10px 0;
}

/* 代码块高亮 */
pre {
  position: relative;
  margin-bottom: 24px;
  border-radius: 3px;
  border: 1px solid #c3ccd0;
  background: #fff;
  overflow: hidden;
}
code {
  display: block;
  padding: 12px 24px;
  overflow-y: auto;
  font-weight: 300;
  font-family: Menlo, monospace;
}
code.has-numbering {
  margin-left: 21px;
}
.pre-numbering {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  padding: 12px 2px 12px 0;
  border-right: 1px solid #c3ccd0;
  border-radius: 3px 0 0 3px;
  background-color: #eee;
  text-align: right;
  font-family: Menlo, monospace;
  font-size: 0.8em;
  color: #aaa;
}

h1.entry-title {
  font-size: 23px;
  font-weight: 600;
  color: #737373;
}

.entry-header hr {
  width: 100%;
  height: 1px;
  border: 0;
  background: #efefef;
  margin: 15px 0;
}

hr {
  width: 100%;
  height: 1px;
  border: 0;
  background: #efefef;
  margin: 15px 0;
}

ul > h2 {
  font-weight: 500;
  margin: 50px 0;
}
