.post-item {
  position: relative;
  display: flex;
}

.post-item .img-box {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 50%;
  padding: 2px;
  border: 1px solid #dadada;
  flex: 0 0 100px;
}

.post-item .img-box img {
  width: 100%;
  border-radius: 50%;
}

.post-item header {
  display: flex;
}

.post-item header h1 {
  flex: 1 1;
  margin: 0;
  font-weight: 500;
  font-size: 22px;
}

.post-item .content {
  padding-left: 15px;
  flex: 1 1;
}

.post-list {
  display: block;
  margin-bottom: 30px;
}

.post-list hr {
  width: 30%;
  height: 1px;
  margin: 0 auto;
  border: 0;
  background: #efefef;
}

.post-more {
  height: 30px;
  text-align: right;
}

.notice {
  padding: 20px;
  font-family: miranafont, "Hiragino Sans GB", STXihei, "Microsoft YaHei",
    SimSun, sans-serif;
  color: #505050;
  position: relative;
  margin-bottom: 60px;
  background: #fbfbfb;
  border-radius: 3px;
  width: 800px;
  margin: 0 auto 60px auto;
}

.top-feature {
  width: 800px;
  height: auto;
  margin: 0 auto;
  font-family: miranafont, "Hiragino Sans GB", STXihei, "Microsoft YaHei",
    SimSun, sans-serif;
}
.top-feature li {
  width: 32.9%;
  float: left;
  margin: 0 auto;
  position: relative;
  display: block;
}

.top-feature li:nth-child(2) {
  margin: 0 5px;
}

.top-feature img {
  height: 160px;
  width: 257px;
  object-fit: cover;
  border-radius: 5px;
}

h1.fes-title,
h1.main-title {
  color: #757575;
  font-size: 16px;
  font-weight: 500;
  padding-bottom: 15px;
  border-bottom: 1px dashed #ececec;
}

.feature-content {
  margin-top: 10px;
  display: inline-block;
  position: relative;
}

.feature-title span {
  position: absolute;
  width: 100%;
  height: 160px;
  text-align: center;
  line-height: 160px;
  background: #bbe2db;
  color: #fff;
  font-size: 16px;
  opacity: 0;
  border-radius: 5px;
}

.feature-title:hover span {
  position: absolute;
  width: 100%;
  height: 160px;
  text-align: center;
  line-height: 160px;
  background: #bbe2db;
  color: #fff;
  font-size: 16px;
  opacity: 1;
  border-radius: 5px;
}

.foverlay {
  transition: opacity 0.4s ease-out;
}

hr {
  width: 100%;
  height: 1px;
  border: 0;
  background: #efefef;
  margin: 15px 0;
}

ul > h2 {
  font-weight: 500;
  margin: 50px 0;
}

pre > code {
  color: #333;
  background: #f8f8f8;
}
pre > code {
  font-size: 12px;
  padding: 15px 12px;
  margin: 0;
  word-break: normal;
  display: block;
  overflow-x: auto;
  color: #333;
  background: #f8f8f8;
}
pre {
  font-family: Menlo, Monaco, Consolas, Courier New, monospace;
}
code {
  word-break: break-word;
  border-radius: 2px;
  overflow-x: auto;
  background-color: #fff5f5;
  color: #ff502c;
  font-size: 0.87em;
  padding: 0.065em 0.4em;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

p code {
  padding: 10px;
  margin: 10px 0;
}

/* 代码块高亮 */
pre {
  position: relative;
  margin-bottom: 24px;
  border-radius: 3px;
  border: 1px solid #c3ccd0;
  background: #fff;
  overflow: hidden;
}
code {
  display: block;
  padding: 12px 24px;
  overflow-y: auto;
  font-weight: 300;
  font-family: Menlo, monospace;
}
code.has-numbering {
  margin-left: 21px;
}
.pre-numbering {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  padding: 12px 2px 12px 0;
  border-right: 1px solid #c3ccd0;
  border-radius: 3px 0 0 3px;
  background-color: #eee;
  text-align: right;
  font-family: Menlo, monospace;
  font-size: 0.8em;
  color: #aaa;
}

h1.entry-title {
  font-size: 23px;
  font-weight: 600;
  color: #737373;
}

.entry-header hr {
  width: 100%;
  height: 1px;
  border: 0;
  background: #efefef;
  margin: 15px 0;
}

hr {
  width: 100%;
  height: 1px;
  border: 0;
  background: #efefef;
  margin: 15px 0;
}

ul > h2 {
  font-weight: 500;
  margin: 50px 0;
}

::-webkit-scrollbar {
	width: 0;
	background: 0 0;
}

::selection {
	background: rgba(204, 204, 204, 0.18);
	color: #000;
}

html body {
	_background-attachment: fixed;
}

html {
	width: 100%;
	height: 100%;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

body {
	width: 100%;
	height: 100%;
	font-family: PingFang SC, 'Hiragino Sans GB', 'Source Han Sans CN', Roboto, 'Microsoft Yahei', sans-serif;
	font-weight: 400;
	font-size: 16px;
	color: rgba(2, 0, 0, 0.6);
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
blockquote,
p,
div,
span,
ul,
ol,
li,
form,
input,
textarea,
br,
button {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
}

*,
*:after,
*:before {
	box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5 {
	margin: 0;
}

h1 {
	font-size: 20px;
	line-height: 48px;
	margin-bottom: 24px;
}

a {
	color: #807f83;
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}

a:active,
a:hover {
	outline: 0;
}

blockquote {
	overflow: hidden;
}

ul,
ol {
	list-style: none;
}

img {
	height: auto;
	max-width: 100%;
}

ol,
ul {
	list-style: none;
}

img {
	max-width: 100%;
	height: auto;
	transition: opacity 0.2s;
}

.clearfix:before,
.clearfix:after {
	content: ' ';
	display: table;
}

.clearfix:after {
	clear: both;
}

.back-top {
	position: fixed;
	bottom: 50px;
	right: 0;
	z-index: 2;
	display: none;
}

.back-top i {
	font-size: 50px;
}

.blog-description {
	text-transform: none;
	font-weight: 400;
}

.custom-logo {
	display: inline-block;
	width: 100px;
}

.site-branding {
	position: relative;
	display: inline-block;
	z-index: 3;
	padding-top: 100px;
}

#xiaoshubao::before {
	position: fixed;
	width: 100%;
	height: 100%;
	background-size: cover;
	background-position: top center;
	z-index: -999;
	content: '';
	display: block;
	background-image: url(/static/media/47fb3c_.3b05ec55.jpg);
}

#particles-js {
	position: absolute;
	top: -100px;
	width: 100%;
	height: 100%;
}

#masthead {
	position: relative;
	z-index: 2;
	display: block;
	color: #fff;
	text-align: center;
	width: 100%;
	height: 450px;
	box-sizing: border-box;
}

#main {
	background: #fff;
	position: relative;
	z-index: 2;
}

.container {
	position: relative;
	background: #fff;
	width: 800px;
	margin: 0 auto;
	overflow: hidden;
}

.decor-wrapper {
	width: 100%;
	height: 50%;
	position: absolute;
	left: 0;
	bottom: 0;
	z-index: 2;
}

.decor {
	position: absolute;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 2;
}

.decor.bottom {
	bottom: -1px;
}

.decor.top {
	top: -1px;
}

.avatar img {
	border-radius: 50%;
}

article {
	width: 800px;
	margin: 100px auto 0 auto;
}

.top-social {
	height: 32px;
	list-style: none;
	display: inline-block;
	margin-top: 20px;
}

.top-social li {
	float: left;
	margin-right: 10px;
}

.top-social img {
	height: 32px;
	width: 32px;
	background: #fff;
	border-radius: 50%;
}

